	module.exports = "<div id=question-detail> <sheet-pannel :sheetshow.sync=sheetshow :center=true> <div slot=sheet-button> <mdl-button primary raised slot=sheet-button class=sheet-button @click=\"sheetshow = true\" v-show=\"!sheetshow && edit.button\" :disabled=!edit.button> 修改題目信息 </mdl-button> </div> <div slot=button-subtitle> <p style=\"margin: 8px 0 0 0;color: #9E9E9E\">最後更新于 {{ details.updated_at | date 'YYYY[年]M[月]DD[日] h:mm a'}}</p> </div> <div slot=sheet-zone> <div class=\"flex-row flex-center\"> <span>科目：</span> <select v-model=details.subject> <option v-for=\"subject in subjects\" v-bind:value=subject.id> {{ subject.name }} </option> </select> </div> <div class=\"difficulty-box flex-row flex-center\"> <span style=line-height:26px>難度：</span> <span class=\"flex-row flex-baseline\"> <i class=material-icons v-for=\"1 in 5\" @click=\"details.difficulty = $index + 1\" :class=\"{'difficulty-heighlight': details.difficulty > $index}\">star_rate</i> </span> </div> <div class=flex-column style=position:relative;top:-20px> <div v-show=\"details.tags && details.tags.length !== 0\" style=\"padding-top: 25px;margin-right: 10px\"> <span>標籤：</span> <span class=q-d-tag @click=\"tags('remove', null, $index)\" v-for=\"tag in details.tags\" track-by=$index>{{tag}}</span> </div> <mdl-textfield label=輸入標籤.回車 @keyup.enter=\"tags('add', edit.tag)\" :value.sync=edit.tag style=width:200px></mdl-textfield> </div> <div style=position:relative;top:-15px;left:-10px> <mdl-button primary accent raised class=sheet-button @click=updateInfo()> 提交修改 </mdl-button> <mdl-button class=sheet-button @click=cancelUpdate()> 取消 </mdl-button> </div> </div> </sheet-pannel> <div id=question-body> <div v-if=\"details.type === 'mc'\"> <mdl-button accent raised style=\"margin:0 0 8px 0\" @click=getQuestionAnswer() v-show=!answer.get :disabled=answer.buttonDisable>顯示答案</mdl-button> <card> <div slot=content style=padding:16px> <div class=q-d-info-wrapper> <span class=q-d-subject>{{details.subject | subject}}</span> <div class=q-d-difficulty> <i class=material-icons v-for=\"i in getNumberArray(details.difficulty)\" track-by=$index>star_rate</i> </div> </div> {{{details.context}}} </div> </card> <div v-if=\"details.type === 'mc'\" class=\"q-d-mc-wrapper flex-column\"> <div class=flex-row> <card class=flex-50 :class=\"{'hightlight-answer': answer.results === 0, 'wrong': answer.results !== 0 && choice === 0}\" @click=checkMc(0)><div slot=content><span class=mc-choice-label>A</span>{{{details.choices[0]}}}</div></card> <card class=flex-50 :class=\"{'hightlight-answer': answer.results === 1, 'wrong': answer.results !== 1 && choice === 1}\" @click=checkMc(1)><div slot=content><span class=mc-choice-label>B</span>{{{details.choices[1]}}}</div></card> </div> <div class=flex-row> <card class=flex-50 :class=\"{'hightlight-answer': answer.results === 2, 'wrong': answer.results !== 2 && choice === 2}\" @click=checkMc(2)><div slot=content><span class=mc-choice-label>C</span>{{{details.choices[2]}}}</div></card> <card class=flex-50 :class=\"{'hightlight-answer': answer.results === 3, 'wrong': answer.results !== 3 && choice === 3}\" @click=checkMc(3)><div slot=content><span class=mc-choice-label>D</span>{{{details.choices[3]}}}</div></card> </div> </div> </div> </div> </div>";

